/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import React, { useEffect, useState } from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import { PaymentMothod, PaymentProcessor, ProcessingCompany } from 'Utility/ListCollection';
import { PaymentProcessingInit } from 'Model/SetupScreen.model';
import * as StringHelpers from 'Helpers/StringHelpers';
import { InputSelect } from 'Components/Utility/CustomSelect';
import { InputCheckBox } from 'Components/Utility/CustomSelect';

const PaymentProcessing = (props) => {

    const [value, setValue] = useState(0);
    const [PaymentProcessing, setPaymentProcessing] = useState(PaymentProcessingInit);

    const [PaymentMothodList, setPaymentMothodList] = useState(PaymentMothod);
    const [ProcessingCompanyList, setProcessingCompanyList] = useState(ProcessingCompany);
    const [PaymentProcessorList, setPaymentProcessorList] = useState(PaymentProcessor);

    useEffect(() => {
        setPaymentProcessing({
            ...PaymentProcessing,
            terminal: StringHelpers.UpdateObject(PaymentProcessing.terminal, props.SetupScreenVal.terminal)
        });
    }, [props.SetupScreenVal]);

    const ChangeHandler = (evt) => {
        const value = evt.target.value;
        setPaymentProcessing({
            ...PaymentProcessing,
            terminal: { ...PaymentProcessing.terminal, [evt.target.name]: value }
        });
    };

    const handletabChange = (event, newValue) => setValue(newValue);

    function a11yProps(index) {
        return {
            id: `simple-tab-${index}`,
            'aria-controls': `simple-tabpanel-${index}`,
        };
    }



    return (
        <div id='pos_view' className='payment-page'>
            <div className='top-filter-row filter-tw'>
                <div className='lt-title-bx'>
                    <h2>Payment Processing</h2>
                </div>
                <div className='rt-filter-bx filter-hd'>
                    <div className='row searchbar-mainview'>
                        <div className='icons-list'>
                            <button className='icons-hd-cm checks-icons' onClick={() => {
                                props.UpdateSetting(PaymentProcessing);
                            }}></button>
                        </div>
                    </div>
                </div>
            </div>
            <hr className='full-width' />
            <div className='row table-cm mt-0 d-none'>
                <div className="col-md-5 mt-4">
                    <div className='form-group department-row'>
                        <div className='row'>
                            <label htmlFor='departmentId' className='col-sm-3 col-form-label'>Processing</label>
                            <div className='col-md-8' style={{ display: 'flex' }}>
                                <InputSelect options={PaymentMothodList} className='product-depatment'
                                    name='processingType'
                                    value={PaymentMothodList.filter(({ value }) => value === PaymentProcessing.terminal.processingType)}
                                    onChange={(e) => {
                                        ChangeHandler(e);
                                    }}
                                />
                            </div>
                        </div>
                    </div>

                </div>
            </div>
            <Box className='tabs-cm tab-tw' sx={{ width: '100%' }}>
                <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                    <Tabs value={value} onChange={handletabChange} aria-label="basic tabs example">
                        <Tab label="Processor Config" {...a11yProps(0)} />
                        <Tab label="Unlock" {...a11yProps(1)} />
                    </Tabs>
                </Box>
                <Box className='tabs-panel'>
                    {value === 0 && (
                        <div className='row table-cm mt-0'>
                            <div className="col-md-5">
                                <div className='form-group department-row'>
                                    <div className='row'>
                                        <label htmlFor='departmentId' className='col-sm-4 col-form-label'>Payment Processor</label>
                                        <div className='col-md-8' style={{ display: 'flex' }}>
                                            <InputSelect options={PaymentProcessorList} className='product-depatment'
                                                name='paymentProcessorType'
                                                value={PaymentProcessorList.filter(({ value }) => value === PaymentProcessing.terminal.paymentProcessorType)}
                                                isClearable={''}
                                                onChange={(e) => {
                                                    ChangeHandler(e);
                                                }}
                                            />

                                        </div>
                                    </div>
                                </div>

                            </div>
                            <div className="col-md-12 mb-3">
                                <hr className='full-width mt-4' />
                            </div>
                            <div className='d-flex row'>
                                <div className='form-group col-md-4 mb-3'>
                                    <div className='row'>
                                        <label className='col-sm-4 col-form-label'>IP Address</label>
                                        <div className='col-md-8'>
                                            <input type='text'
                                                value={PaymentProcessing.terminal.ipAddress}
                                                onChange={(e) => {
                                                    ChangeHandler(e);
                                                }}
                                                name='ipAddress'
                                                placeholder='10.1.10.43'
                                                className='form-control' />
                                        </div>
                                    </div>
                                </div>
                                <div className="form-group col-md-4 mb-3 d-none">
                                    <div className='form-group department-row'>
                                        <div className='row'>
                                            <label htmlFor='departmentId' className='col-sm-5 col-form-label'>Payment Company</label>
                                            <div className='col-md-7' style={{ display: 'flex' }}>
                                                <InputSelect options={ProcessingCompanyList} className='product-depatment'
                                                    name='processingCompany'
                                                    value={ProcessingCompanyList.filter(({ value }) => value === PaymentProcessing.terminal.processingCompany)}
                                                    isClearable={''}
                                                    onChange={(e) => {
                                                        ChangeHandler(e);
                                                    }}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className='form-group col-md-4 mb-3'>
                                    <div className='row'>
                                        <label className='col-sm-4 col-form-label'>Port No</label>
                                        <div className='col-md-8'>
                                            <input type='text'
                                                name='portNo'
                                                value={PaymentProcessing.terminal.portNo}
                                                onChange={(e) => {
                                                    ChangeHandler(e);
                                                }}
                                                placeholder='1009'
                                                className='form-control' />
                                        </div>
                                    </div>
                                </div>
                                <div className='form-group col-md-4 mb-3'>
                                    <div className='row'>
                                        <label className='col-sm-4 col-form-label'>Timeout Seconds</label>
                                        <div className='col-md-8'>
                                            <input type='text'
                                                name='timeoutSec'
                                                value={PaymentProcessing.terminal.timeoutSec}
                                                onChange={(e) => {
                                                    ChangeHandler(e);
                                                }}
                                                placeholder='1009'
                                                className='form-control' />
                                        </div>
                                    </div>
                                </div>

                                <div className="col-md-4 mt-2">
                                    <div className="info-bx info-bx-width">
                                        <div className="form-check">
                                            <label className="form-check-label">Process Offline(Store And Forward)
                                            </label>
                                            <InputCheckBox className="form-check-input"
                                                name='isProcessDebitCards'
                                                checked={PaymentProcessing.terminal.isProcessDebitCards}
                                                onChange={(e) => {
                                                    ChangeHandler(e);
                                                }} />

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>)}
                    {value === 1 && (
                        <div className='row table-cm mt-0'>
                            <div className='form-group col-md-4 mb-3'>
                                <div className='row'>
                                    <label className='col-sm-4 col-form-label'>Merchant Number</label>
                                    <div className='col-md-8'>
                                        <input type='text'
                                            name='merchantNumber'
                                            placeholder='498386089888'
                                            value={PaymentProcessing.terminal.merchantNumber}
                                            onChange={(e) => {
                                                ChangeHandler(e);
                                            }}
                                            className='form-control' />
                                    </div>
                                </div>
                            </div>

                            <div className='form-group col-md-4 mb-3'>
                                <div className='row'>
                                    <label className='col-sm-4 col-form-label'>Processor</label>
                                    <div className='col-md-8'>
                                        <input type='text'
                                            name='processor'
                                            placeholder='PAX - FDMS Rapid Connect'
                                            value={PaymentProcessing.terminal.processor}
                                            onChange={(e) => {
                                                ChangeHandler(e);
                                            }}
                                            className='form-control' />
                                    </div>
                                </div>
                            </div>

                            <div className='form-group col-md-4 mb-3'>
                                <div className='row'>
                                    <label className='col-sm-4 col-form-label'>Sit ID</label>
                                    <div className='col-md-8'>
                                        <input type='text'
                                            name='siteID'
                                            placeholder='27D248F8FE585497FA9368CCF6'
                                            value={PaymentProcessing.terminal.siteID}
                                            onChange={(e) => {
                                                ChangeHandler(e);
                                            }}
                                            className='form-control' />
                                    </div>
                                </div>
                            </div>
                            <div className='form-group col-md-4 mb-3'>
                                <div className='row'>
                                    <label className='col-sm-4 col-form-label'>Unlock Code</label>
                                    <div className='col-md-8'>
                                        <textarea name="unlockCode"
                                            value={PaymentProcessing.terminal.unlockCode}
                                            onChange={(e) => {
                                                ChangeHandler(e);
                                            }}
                                            className='form-control' />
                                    </div>
                                </div>
                            </div>
                        </div>)}
                </Box>
            </Box>
        </div>
    );
};

export default PaymentProcessing;