/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import React, { useEffect, useState } from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Select from 'react-select';
import { InputSelect } from 'Components/Utility/CustomSelect';
import { ReceiptInit } from 'Model/SetupScreen.model';
import { ReceiptSize, PrintInvoice, ProfLogo, YesNo } from 'Utility/ListCollection';
import * as StringHelpers from 'Helpers/StringHelpers';
import { InputCheckBox } from 'Components/Utility/CustomSelect';

const Receipt = (props) => {

    const [ReceiptSizeList, PrintInvoiceList, ProfLogoList, YesNoList] = [ReceiptSize, PrintInvoice, ProfLogo, YesNo];
    const [value, setValue] = useState(0);
    const [Receipt, setReceipt] = useState(ReceiptInit);

    useEffect(() => {
        setReceipt({
            ...Receipt,
            store: StringHelpers.UpdateObject(Receipt.store, props.SetupScreenVal.store),
            terminal: StringHelpers.UpdateObject(Receipt.terminal, props.SetupScreenVal.terminal)
        });
    }, [props.SetupScreenVal]);

    const ChangeStoreHandler = (evt) => {
        const value = evt.target.value;
        setReceipt({
            ...Receipt,
            store: { ...Receipt.store, [evt.target.name]: value }
        });
    };

    const ChangeTerminalHandler = (evt) => {
        const value = evt.target.value;
        setReceipt({
            ...Receipt,
            terminal: { ...Receipt.terminal, [evt.target.name]: value }
        });
    };

    const handletabChange = (event, newValue) => setValue(newValue);
    function TabPanel(props) {
        // const { children, value, index, ...other } = props;
        return (
            <div
                role='tabpanel'
                hidden={props.value !== props.index}
                id={`simple-tabpanel-${props.index}`}
                aria-labelledby={`simple-tab-${props.index}`}
                {...props}
            >
                {props.value === props.index && (
                    <Box sx={{ p: 3 }}>
                        <Typography>{props.children}</Typography>
                    </Box>
                )}
            </div>
        );
    }
    function a11yProps(index) {
        return {
            id: `simple-tab-${index}`,
            'aria-controls': `simple-tabpanel-${index}`,
        };
    }
    return (
        <div id='pos_view' className='recepit-page'>
            <div className='top-filter-row filter-tw'>
                <div className='lt-title-bx'>
                    <h2>Receipt</h2>
                </div>
                <div className='rt-filter-bx filter-hd'>
                    <div className='row searchbar-mainview'>
                        <div className='icons-list'>
                            <button className='icons-hd-cm checks-icons' onClick={() => {
                                props.UpdateSetting(Receipt);
                            }}></button>
                        </div>
                    </div>
                </div>
            </div>
            <hr className='full-width' />
            <Box className='tabs-cm tab-tw' sx={{ width: '100%' }}>
                <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                    <Tabs value={value} onChange={handletabChange} aria-label="basic tabs example">
                        <Tab label="page 1" {...a11yProps(0)} />
                        <Tab label="page 2" {...a11yProps(1)} />
                    </Tabs>
                </Box>
                <Box className='tabs-panel'>
                    {value === 0 && (
                        <div id='pos_view' className='page-1'>
                            <div className='row table-cm mt-0'>
                                <div className="col-md-4">
                                    <div className='form-group department-row'>
                                        <div className='row'>
                                            <label htmlFor='departmentId' className='col-sm-4 col-form-label'>Receipt Size</label>
                                            <div className='col-md-8' style={{ display: 'flex' }}>
                                                <InputSelect options={ReceiptSizeList} className='product-depatment'
                                                    name='receiptSize'
                                                    value={ReceiptSizeList.filter(({ value }) => value === Receipt.terminal.receiptSize)}
                                                    onChange={(e) => {
                                                        ChangeTerminalHandler(e);
                                                    }}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div className='form-group department-row'>
                                        <div className='row'>
                                            <label htmlFor='departmentId' className='col-sm-4 col-form-label'>Print Invoices</label>
                                            <div className='col-md-8' style={{ display: 'flex' }}>
                                                <InputSelect options={PrintInvoiceList} className='product-depatment'
                                                    name='printInvoices'
                                                    value={PrintInvoiceList.filter(({ value }) => value === Receipt.terminal.printInvoices)}
                                                    onChange={(e) => {
                                                        ChangeTerminalHandler(e);
                                                    }}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div className='form-group department-row'>
                                        <div className='row'>
                                            <label htmlFor='departmentId' className='col-sm-4 col-form-label'>Prof Logo</label>
                                            <div className='col-md-8' style={{ display: 'flex' }}>
                                                <InputSelect options={ProfLogoList} className='product-depatment'
                                                    name='profLogo'
                                                    value={ProfLogoList.filter(({ value }) => value === Receipt.terminal.profLogo)}
                                                    onChange={(e) => {
                                                        ChangeTerminalHandler(e);
                                                    }}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-12 mb-3">
                                    <hr className='full-width mt-4' />
                                </div>
                                <div className="col-md-4 d-none">
                                    <div className="info-bx info-bx-width">
                                        <div className="form-check">
                                            <label className="form-check-label">Prompt to Email Purchase Order</label>
                                            <InputCheckBox className="form-check-input"
                                                name='promptEmailPO'
                                                checked={Receipt.terminal.promptEmailPO}
                                                onChange={(e) => {
                                                    ChangeTerminalHandler(e);
                                                }} />
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-4 d-none">
                                    <div className="info-bx info-bx-width">
                                        <div className="form-check">
                                            <label className="form-check-label">Print Bar Code On On-Hold Receipts</label>
                                            <InputCheckBox className="form-check-input"
                                                name='onHoldReceipts'
                                                checked={Receipt.terminal.onHoldReceipts}
                                                onChange={(e) => {
                                                    ChangeTerminalHandler(e);
                                                }} />
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div className="info-bx info-bx-width">
                                        <div className="form-check">
                                            <label className="form-check-label">Print Bar Code On Complete Receipts</label>
                                            <InputCheckBox className="form-check-input"
                                                name='completeReceipts'
                                                checked={Receipt.terminal.completeReceipts}
                                                onChange={(e) => {
                                                    ChangeTerminalHandler(e);
                                                }} />
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-4 d-none">
                                    <div className="info-bx info-bx-width">
                                        <div className="form-check">
                                            <label className="form-check-label">Combine Lines on Short Receipt</label>
                                            <InputCheckBox className="form-check-input"
                                                name='shortReceipt'
                                                checked={Receipt.terminal.shortReceipt}
                                                onChange={(e) => {
                                                    ChangeTerminalHandler(e);
                                                }} />
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div className="info-bx info-bx-width">
                                        <div className="form-check">
                                            <label className="form-check-label">Display inventory item special pricing </label>
                                            <InputCheckBox className="form-check-input"
                                                name='specialPricing'
                                                checked={Receipt.terminal.specialPricing}
                                                onChange={(e) => {
                                                    ChangeTerminalHandler(e);
                                                }} />
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-12 mb-2">
                                    <hr className='full-width mt-1' />
                                </div>
                                <div className="col-md-12 mb-3">
                                    <span className="title-tab">Number of extra copies by payment type</span>
                                </div>
                                <div className="col-md-4">
                                    <div className="info-bx info-bx-width-18">
                                        <div className="form-check">
                                            <label className="form-check-label">Suppress Extra Signature Copy</label>
                                            <InputCheckBox className="form-check-input"
                                                name='supressExtraSignCopy'
                                                checked={Receipt.terminal.supressExtraSignCopy}
                                                onChange={(e) => {
                                                    ChangeTerminalHandler(e);
                                                }} />
                                        </div>
                                    </div>
                                </div>
                                <div className='clearfix'></div>
                                <div className='form-group col-md-4'>
                                    <div className='row'>
                                        <label className='col-sm-4 col-form-label'>Credit</label>
                                        <div className='col-md-5'>
                                            <input type='text'
                                                name='paymentTypeCredit'
                                                value={Receipt.terminal.paymentTypeCredit}
                                                pattern="[0-9]*"
                                                onChange={(e) => {
                                                    if (e.target.validity.valid) {
                                                        ChangeTerminalHandler(e);
                                                    }
                                                }}
                                                placeholder='00'
                                                className='form-control' />
                                        </div>
                                    </div>
                                </div>
                                <div className='form-group col-md-4'>
                                    <div className='row'>
                                        <label className='col-sm-4 col-form-label'>Debit</label>
                                        <div className='col-md-5'>
                                            <input type='text'
                                                name='paymentTypeDebit'
                                                value={Receipt.terminal.paymentTypeDebit}
                                                pattern="[0-9]*"
                                                onChange={(e) => {
                                                    if (e.target.validity.valid) {
                                                        ChangeTerminalHandler(e);
                                                    }
                                                }}
                                                placeholder='00'
                                                className='form-control' />
                                        </div>
                                    </div>
                                </div>
                                <div className='form-group col-md-4'>
                                    <div className='row'>
                                        <label className='col-sm-4 col-form-label'>EBT</label>
                                        <div className='col-md-5'>
                                            <input type='text'
                                                name='paymentTypeEBT'
                                                value={Receipt.terminal.paymentTypeEBT}
                                                pattern="[0-9]*"
                                                onChange={(e) => {
                                                    if (e.target.validity.valid) {
                                                        ChangeTerminalHandler(e);
                                                    }
                                                }}
                                                placeholder='00'
                                                className='form-control' />
                                        </div>
                                    </div>
                                </div>
                                <div className='form-group col-md-4 mt-3'>
                                    <div className='row'>
                                        <label className='col-sm-4 col-form-label'>Gift</label>
                                        <div className='col-md-5'>
                                            <input type='text'
                                                name='paymentTypeGift'
                                                value={Receipt.terminal.paymentTypeGift}
                                                pattern="[0-9]*"
                                                onChange={(e) => {
                                                    if (e.target.validity.valid) {
                                                        ChangeTerminalHandler(e);
                                                    }
                                                }}
                                                placeholder='00'
                                                className='form-control' />
                                        </div>
                                    </div>
                                </div>
                                <div className='form-group col-md-4 mt-3'>
                                    <div className='row'>
                                        <label className='col-sm-4 col-form-label'>Cash</label>
                                        <div className='col-md-5'>
                                            <input type='text'
                                                name='paymentTypeCash'
                                                value={Receipt.terminal.paymentTypeCash}
                                                pattern="[0-9]*"
                                                onChange={(e) => {
                                                    if (e.target.validity.valid) {
                                                        ChangeTerminalHandler(e);
                                                    }
                                                }}
                                                placeholder='00'
                                                className='form-control' />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>)}

                    {value === 1 && (
                        <div id='pos_view' className='page-1'>
                            <div className='row table-cm mt-0'>
                                <div className="col-md-4">
                                    <div className='form-group col-md-12 department-row'>
                                        <div className='row'>
                                            <label htmlFor='departmentId' className='col-sm-5 col-form-label'>Print Customer Info</label>
                                            <div className='col-md-7' style={{ display: 'flex' }}>
                                                <InputSelect options={YesNoList} className='product-depatment'
                                                    name='printCustInfo'
                                                    value={YesNoList.filter(({ value }) => value === Receipt.store.printCustInfo)}
                                                    onChange={(e) => {
                                                        ChangeStoreHandler(e);
                                                    }}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div className='form-group col-md-12 department-row'>
                                        <div className='row'>
                                            <label htmlFor='departmentId' className='col-sm-5 col-form-label'>Print Amt Discount</label>
                                            <div className='col-md-7' style={{ display: 'flex' }}>
                                                <InputSelect options={YesNoList} className='product-depatment'
                                                    name='printAmtSaved'
                                                    value={YesNoList.filter(({ value }) => value === Receipt.store.printAmtSaved)}
                                                    onChange={(e) => {
                                                        ChangeStoreHandler(e);
                                                    }}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-4 d-none">
                                    <div className='form-group col-md-12 department-row'>
                                        <div className='row'>
                                            <label htmlFor='departmentId' className='col-sm-5 col-form-label'>Print Acct Balance</label>
                                            <div className='col-md-7' style={{ display: 'flex' }}>
                                                <InputSelect options={YesNoList} className='product-depatment'
                                                    name='printAccBal'
                                                    value={YesNoList.filter(({ value }) => value === Receipt.store.printAccBal)}
                                                    onChange={(e) => {
                                                        ChangeStoreHandler(e);
                                                    }}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>)}

                </Box>
            </Box>
        </div>
    );
};
export default Receipt;