/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import Select from 'react-select';
import { QuickAddInit } from 'Model/SetupScreen.model';
import * as StringHelpers from 'Helpers/StringHelpers';
import * as DepartmentAPI from 'Services/Department';
import * as VendorAPI from 'Services/Vendor';
import { InputSelect } from 'Components/Utility/CustomSelect';
import { InputCheckBox } from 'Components/Utility/CustomSelect';

const QuickAdd = (props) => {

    const [QuickAdd, setQuickAdd] = useState(QuickAddInit);
    const [DepartmentList, setDepartmentList] = useState([]);
    const [Vendorslist, setVendorslist] = useState([]);
    

    const GetAllDepartment = () => DepartmentAPI.DepartmentDropDownList().then(res => { setDepartmentList(res); }).catch(err => { });
    const LoadVendor = () => VendorAPI.GetAllVenderDropDown().then(res => { setVendorslist(res); }).catch();

    const ChangeStoreHandler = (evt) => {
        const value = evt.target.value;
        setQuickAdd({
          ...QuickAdd,
          store: { ...QuickAdd.store, [evt.target.name]: value }
        });
      };
    
      const ChangeTerminalHandler = (evt) => {
        const value = evt.target.value;
        setQuickAdd({
          ...QuickAdd,
          terminal: { ...QuickAdd.terminal, [evt.target.name]: value }
        });
      };

    useEffect(() => {
        (async () => {
            await GetAllDepartment();
            await LoadVendor();
        })();

        return () => {
            // this now gets called when the component unmounts
        };

    }, []);

    useEffect(() => {
        setQuickAdd({
            ...QuickAdd,
            store: StringHelpers.UpdateObject(QuickAdd.store, props.SetupScreenVal.store),
            terminal: StringHelpers.UpdateObject(QuickAdd.terminal, props.SetupScreenVal.terminal)
        });
    }, [props.SetupScreenVal]);

    return (
        <div id='pos_view' className='quickadd-screen-page'>
            <div className='top-filter-row filter-tw'>
                <div className='lt-title-bx'>
                    <h2>Quick Add</h2>
                </div>
                <div className='rt-filter-bx filter-hd'>
                    <div className='row searchbar-mainview'>
                        <div className='icons-list'>
                            <button className='icons-hd-cm checks-icons' onClick={() => {
                                props.UpdateSetting(QuickAdd);
                            }}></button>
                        </div>
                    </div>
                </div>
            </div>
            <hr className='full-width' />
            <div className='row table-cm mt-0'>
                <div className="col-md-12 mb-4 d-flex">
                    <span className="title-tab mb-0">If bar code scanned is not found...</span>
                </div>
                <div className="col-md-4">
                    <div className="info-bx info-bx-width">
                        <div className="form-check">
                            <label className="form-check-label">Display Item Not Found message</label>
                            <input className="form-check-input" 
                            checked={true}
                            disabled={true}
                            type="checkbox"></input>
                        </div>
                    </div>
                </div>
                <div className="col-md-12 mb-3">
                    <hr className='full-width mt-2' />
                </div>
                <div className="col-md-4">
                    <div className='form-group  department-row'>
                        <div className='row'>
                            <label htmlFor='departmentId' className='col-sm-12 col-form-label'>Default Department</label>
                            <div className='col-md-12' style={{ display: 'flex' }}>
                                <InputSelect options={DepartmentList} className='product-depatment'
                                    name='defaultDepartment'
                                    value={DepartmentList.filter(({ value }) => value === QuickAdd.store.defaultDepartment)}
                                    onChange={(e) => {
                                        ChangeStoreHandler(e);
                                    }}
                                />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div className='form-group  department-row'>
                        <div className='row'>
                            <label htmlFor='departmentId' className='col-sm-12 col-form-label'>Default Vendor</label>
                            <div className='col-md-12' style={{ display: 'flex' }}>
                                <InputSelect options={Vendorslist} className='product-depatment'
                                    name='defaultVendor'
                                    value={Vendorslist.filter(({ value }) => value === QuickAdd.store.defaultVendor)}
                                    onChange={(e) => {
                                        ChangeStoreHandler(e);
                                    }}
                                />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div className='form-group  department-row'>
                        <div className='row'>
                            <label htmlFor='departmentId' className='col-sm-12 col-form-label'>Default Description </label>
                            <div className='col-md-12' style={{ display: 'flex' }}>
                                <input type='text'
                                    name='defaultDescription'
                                    value={QuickAdd.store.defaultDescription}
                                    onChange={(e)=>{
                                        ChangeStoreHandler(e);
                                    }}
                                    placeholder='Default Description New Item'
                                    className='form-control' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default QuickAdd;