/* eslint-disable react/prop-types */
import React from 'react';
import { useState, useEffect } from 'react';
import { InventoryInit } from 'Model/SetupScreen.model';
import * as StringHelpers from 'Helpers/StringHelpers';
import { InputCheckBox } from 'Components/Utility/CustomSelect';
import TextField from '@mui/material/TextField';
import { CusDateFormater } from 'Helpers/StringHelpers';

const Inventory = (props) => {

  const [Inventory, setInventory] = useState(InventoryInit);

  const ChangeHandler = (evt) => {
    const value = evt.target.value;
    setInventory({
      ...Inventory,
      store: { ...Inventory.store, [evt.target.name]: value }
    });
  };

  useEffect(() => {
    setInventory({
      ...Inventory,
      store: StringHelpers.UpdateObject(Inventory.store, props.SetupScreenVal.store)
    });
  }, [props.SetupScreenVal]);

  return (
    <div id='pos_view'>
      <div className='top-filter-row filter-tw'>
        <div className='lt-title-bx'>
          <h2>Inventory</h2>
        </div>
        <div className='rt-filter-bx filter-hd'>
          <div className='row searchbar-mainview'>
            <div className='icons-list'>
              <button className='icons-hd-cm checks-icons' onClick={() => {
                props.UpdateSetting(Inventory);
              }}></button>
            </div>
          </div>
        </div>

      </div>
      <hr className='full-width' />
      <div className='margin-depart table-cm '>
        <div className='row'>
          <div className='col-md-12'>
            <div className='row min-row'>

              <div className="col-md-12 mb-3">
                <span className="title-tab">Average Cost Method</span>
              </div>

              <div className="col-md-4">
                <div className="info-bx">
                  <div className="form-check mb-0">
                    <label className="form-check-label">Disabled</label>
                    <input className="form-check-input"
                      name='avgCost'
                      value={false}
                      checked={Inventory.store.avgCost === false}
                      onChange={() => {
                        setInventory({ ...Inventory, store: { ...Inventory.store, avgCost: false } });
                      }}
                      type="radio"></input>
                  </div>
                </div>
              </div>

              <div className="col-md-4">
                <div className="info-bx">
                  <div className="form-check mb-0">
                    <label className="form-check-label">Enabled</label>
                    <input className="form-check-input"
                      value={true}
                      checked={Inventory.store.avgCost === true}
                      onChange={() => {
                        setInventory({ ...Inventory, store: { ...Inventory.store, avgCost: true } });
                      }}
                      name='avgCost' type="radio"></input>
                  </div>
                </div>
              </div>

              <div className="col-md-12 mb-3">
                <hr className='full-width mt-4' />
              </div>

              <div className="col-md-4 d-none">
                <div className="info-bx info-bx-width">
                  <div className="form-check">
                    <label className="form-check-label">Track Incoming Serial Numbers</label>
                    <InputCheckBox className="form-check-input"
                      name='trackIncomingSN'
                      checked={Inventory.store.trackIncomingSN}
                      onChange={(e) => {
                        ChangeHandler(e);
                      }} />
                  </div>
                </div>
              </div>

              <div className="col-md-4 d-none">
                <div className="info-bx info-bx-width">
                  <div className="form-check">
                    <label className="form-check-label">Require description for Instant POs</label>
                    <InputCheckBox className="form-check-input"
                      name='requireDesc'
                      checked={Inventory.store.requireDesc}
                      onChange={(e) => {
                        ChangeHandler(e);
                      }} />

                  </div>
                </div>
              </div>

              <div className="col-md-4 d-none">
                <div className="info-bx info-bx-width">
                  <div className="form-check">
                    <label className="form-check-label">Prompt for Out Of Date in PO</label>
                    <InputCheckBox className="form-check-input"
                      name='promptforOutOfDate'
                      checked={Inventory.store.promptforOutOfDate}
                      onChange={(e) => {
                        ChangeHandler(e);
                      }} />
                  </div>
                </div>
              </div>

              <div className="col-md-4">
                <div className="info-bx info-bx-width">
                  <div className="form-check">
                    <label className="form-check-label">Check ID -- Birthday Prompt</label>
                    <InputCheckBox className="form-check-input"
                      name='birthdayPrompt'
                      checked={Inventory.store.birthdayPrompt}
                      onChange={(e) => {
                        setInventory({
                          ...Inventory,
                          store: { ...Inventory.store, 
                            'birthdayPrompt': e.target.value,
                            'checkExpiration': e.target.value ? Inventory.store.birthdayPrompt : false
                          }
                        });
                      }} />
                  </div>
                </div>
              </div>

              <div className="col-md-4">
                <div className="info-bx info-bx-width">
                  <div className="form-check">
                    <label className="form-check-label">Check ID -- Check Expiration</label>
                    <InputCheckBox className="form-check-input"
                      disabled={!Inventory.store.birthdayPrompt}
                      name='checkExpiration'
                      checked={Inventory.store.checkExpiration}
                      onChange={(e) => {
                        ChangeHandler(e);
                      }} />
                  </div>
                </div>
              </div>

              <div className="col-md-4">
                <div className="info-bx info-bx-width">
                  <div className="form-check">
                    <label className="form-check-label">Check ID -- Require Customer</label>
                    <InputCheckBox className="form-check-input"
                      name='requireCustomer'
                      checked={Inventory.store.requireCustomer}
                      onChange={(e) => {
                        ChangeHandler(e);
                      }} />
                  </div>
                </div>
              </div>

              <div className="col-md-12 mb-3">
                <hr className='full-width mt-2' />
              </div>


              <div className="col-md-12 mb-4">
                <span className="title-tab">Non birthday prompt message</span>
              </div>

              <div className='form-group col-md-4'>
                <div className='row'>
                  <label className='col-sm-4 col-form-label'>Level 1 -- Age</label>
                  <div className='col-md-8'>
                    <input type='text'
                      name='levelOneAge'
                      value={Inventory.store.levelOneAge}
                      onChange={(e) => {
                        ChangeHandler(e);
                      }}
                      className='form-control' />
                  </div>
                </div>
              </div>

              <div className='form-group col-md-4'>
                <div className='row'>
                  <label className='col-sm-4 col-form-label'>Level 1 -- Prompt</label>
                  <div className='col-md-8'>
                    <input type='text'
                      name='levelOnePrompt'
                      value={Inventory.store.levelOnePrompt}
                      onChange={(e) => {
                        ChangeHandler(e);
                      }}
                      placeholder='Please check ID.'
                      className='form-control' />
                  </div>
                </div>
              </div>

              <div className='clearfix'></div>

              <div className="col-md-4 mt-2">
                <div className="info-bx info-bx-width">
                  <div className="form-check mb-0 mt-2">
                    <label className="form-check-label">Prompt for Military Id exception for age above 18</label>
                    <InputCheckBox className="form-check-input"
                      name='levelOnePromptForMilitary'
                      checked={Inventory.store.levelOnePromptForMilitary}
                      onChange={(e) => {
                        ChangeHandler(e);
                      }} />
                  </div>
                </div>
              </div>


              <div className="col-md-4">
                <div className="info-bx info-bx-width">
                  <div className="form-check mb-0 mt-2">
                    <label className="form-check-label">Allow born on or before</label>
                    <InputCheckBox className="form-check-input"
                      name='levelOneAllowBorn'
                      checked={Inventory.store.levelOneAllowBorn}
                      onChange={(e) => {
                        ChangeHandler(e);
                      }} />
                    <div className=''>
                      <TextField
                        className='data-picker-field Expiration-Coupon'
                        name='levelOneAllowBornDesc'
                        value={Inventory.store.levelOneAllowBornDesc ? CusDateFormater(Inventory.store.levelOneAllowBornDesc) : Inventory.store.levelOneAllowBornDesc}
                        id="datetime-local"
                        type="date"
                        sx={{
                          '& legend': { display: 'none' },
                          '& fieldset': { top: 0 },
                        }}
                        onChange={(e) => {
                          ChangeHandler(e);
                          //props.setCouponData((CouponData) => { return {...CouponData,expiryDate:e.target.value};});
                        }}
                      />
                    </div>
                  </div>

                </div>
              </div>
              <div className='clearfix mt-5'></div>
              <div className='form-group col-md-4'>
                <div className='row'>
                  <label className='col-sm-4 col-form-label'>Level 2 -- Age</label>
                  <div className='col-md-8'>
                    <input type='text'
                      name='levelTwoAge'
                      value={Inventory.store.levelTwoAge}
                      onChange={(e) => {
                        ChangeHandler(e);
                      }}
                      placeholder='21'
                      className='form-control' />
                  </div>
                </div>
              </div>
              <div className='form-group col-md-4'>
                <div className='row'>
                  <label className='col-sm-4 col-form-label'>Level 2 -- Prompt</label>
                  <div className='col-md-8'>
                    <input type='text'
                      value={Inventory.store.levelTwoPrompt}
                      name='levelTwoPrompt'
                      onChange={(e) => {
                        ChangeHandler(e);
                      }}
                      placeholder='Please check ID.'
                      className='form-control' />
                  </div>
                </div>
              </div>
              <div className='clearfix'></div>
              <div className="col-md-4 mt-2">
                <div className="info-bx info-bx-width">
                  <div className="form-check mb-0 mt-2">
                    <label className="form-check-label">Prompt for Military Id exception for age above 18</label>
                    <InputCheckBox className="form-check-input"
                      name='levelTwoPromptForMilitary'
                      checked={Inventory.store.levelTwoPromptForMilitary}
                      onChange={(e) => {
                        ChangeHandler(e);
                      }} />
                  </div>
                </div>
              </div>
              <div className="col-md-4">
                <div className="info-bx info-bx-width">
                  <div className="form-check mb-0 mt-2">
                    <label className="form-check-label">Allow born on or before</label>
                    <InputCheckBox className="form-check-input"
                      name='levelTwoAllowBorn'
                      checked={Inventory.store.levelTwoAllowBorn}
                      onChange={(e) => {
                        ChangeHandler(e);
                      }} />
                    <div className=''>
                    <TextField
                        className='data-picker-field Expiration-Coupon'
                        name='levelTwoAllowBornDesc'
                        value={Inventory.store.levelTwoAllowBornDesc ? CusDateFormater(Inventory.store.levelTwoAllowBornDesc) : Inventory.store.levelTwoAllowBornDesc}
                        id="datetime-local"
                        type="date"
                        sx={{
                          '& legend': { display: 'none' },
                          '& fieldset': { top: 0 },
                        }}
                        onChange={(e) => {
                          ChangeHandler(e);
                        }}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Inventory;