/* eslint-disable no-undef */
/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React from 'react';
import { useState, useEffect } from 'react';
import { HardwareInit } from 'Model/SetupScreen.model';
import swal from 'sweetalert2';
import DefaultConfig from 'Config/GlobalVariable';
import * as StringHelpers from 'Helpers/StringHelpers';
import { DefaultcashDrawerPort } from 'Utility/ListCollection';
import { InputSelect } from 'Components/Utility/CustomSelect';

const Hardware = (props) => {

    const [Hardware, setHardware] = useState(HardwareInit);

    useEffect(() => {
        setHardware({
            ...Hardware,
            terminal: StringHelpers.UpdateObject(Hardware.terminal, props.SetupScreenVal.terminal)
        });
    }, [props.SetupScreenVal]);

    return (<>
        <div id='pos_view' className='hardware-page'>
            <div className='top-filter-row filter-tw'>
                <div className='lt-title-bx'>
                    <h2>Hardware</h2>
                </div>
                <div className='rt-filter-bx filter-hd'>
                    <div className='row searchbar-mainview'>
                        <div className='icons-list'>
                            <button className='icons-hd-cm checks-icons' onClick={() => {
                                props.UpdateSetting(Hardware);
                            }}></button>
                        </div>
                    </div>
                </div>
            </div>
            <hr className='full-width' />
            <div className='margin-depart table-cm '>
                {
                    /* <div className='row min-row'>
                                            <div className='form-group col-md-4'>
                                                <div className='row'>
                                                    <label className='col-sm-4 col-form-label'>IP Address<span>*</span></label>
                                                    <div className='col-md-8'>
                                                        <input type='text'
                                                            value={TerminalInfo.paxIp}
                                                            name='paxIp'
                                                            pattern="[0-9.]*"
                                                            placeholder='IP Address'
                                                            onChange={(e) => { ChangeHandler(e); }}
                                                            className='form-control' />
                                                    </div>
                                                </div>
                                            </div>
                                            <div className='form-group col-md-4'>
                                                <div className='row'>
                                                    <label className='col-sm-4 col-form-label'>Port<span>*</span></label>
                                                    <div className='col-md-8'>
                                                        <input type='text'
                                                            value={TerminalInfo.paxPort}
                                                            name='paxPort'
                                                            pattern="[0-9]*"
                                                            placeholder='90001'
                                                             onChange={(e) => { ChangeHandler(e); }}
                                                            className='form-control' />
                
                                                    </div>
                                                </div>
                                            </div>
                
                                        </div> */
                }
                <div className='row min-row mt-3'>
                    <div className="col-md-6">
                        <div className='row'>
                            <div className="col-md-12 mb-2">
                                <span className="title-tab">Pole Display Type</span>
                            </div>
                            <div className="col-md-6">
                                <div className="info-bx">
                                    <div className="form-check mb-0">
                                        <label className="form-check-label">None</label>
                                        <input
                                            className="form-check-input"
                                            value="0"
                                            checked={Hardware.terminal.poleType === 0}
                                            name="poleType"
                                            onChange={(e) => {
                                                setHardware({ ...Hardware, terminal: { ...Hardware.terminal, poleType: 0 } });
                                            }}
                                            type="radio"
                                        ></input>

                                    </div>

                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="info-bx">
                                    <div className="form-check mb-0">
                                        <label className="form-check-label">General</label>
                                        <input
                                            className="form-check-input"
                                            value="1"
                                            checked={Hardware.terminal.poleType === 1}
                                            name="poleType"
                                            onChange={(e) => {
                                                setHardware({ ...Hardware, terminal: { ...Hardware.terminal, poleType: 1 } });
                                            }}
                                            type="radio"
                                        ></input>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-1"></div>
                    <div className="col-md-3 d-none">
                        <div className='row'>
                            <div className="col-md-12 mb-2">
                                <span className="title-tab">Pole Display Type</span>
                                <hr className='full-width mt-3' />
                            </div>
                            <div className="col-md-6">
                                <div className="info-bx">
                                    <div className="form-check mb-0">
                                        <label className="form-check-label">General</label>
                                        <input
                                            className="form-check-input"
                                            value="1"
                                            name="poleType"
                                            type="radio"
                                        ></input>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-1"></div>
                    <div className="col-md-4 d-none">
                        <div className='row'>
                            <div className="col-md-12 mb-2">
                                <span className="title-tab">Cash Drawer (Reqires Timeclock)</span>
                                <hr className='full-width mt-3' />
                            </div>
                            <div className="col-md-12">
                                <div className="info-bx">
                                    <div className="form-check mb-0">
                                        <label className="form-check-label">Cash Drawer</label>
                                        <input checked={true} className="form-check-input" type="radio"></input>
                                    </div>
                                </div>
                            </div>
                            <div className='clearfix'></div>

                            <div className="col-md-12 mt-3">
                                <div className='form-group department-row'>
                                    <div className='row'>
                                        <label htmlFor='departmentId' className='col-sm-12 col-form-label'>Default Cash Drawer Port
                                        </label>
                                        <div className='col-md-12' style={{ display: 'flex' }}>
                                            <InputSelect
                                                value={DefaultcashDrawerPort.filter(({ value }) => value === Hardware.terminal.defaultCashDrawerPort)}
                                                options={DefaultcashDrawerPort}
                                                onChange={(e) => {
                                                    setHardware({ ...Hardware, terminal: { ...Hardware.terminal, defaultCashDrawerPort: e.target.value } });
                                                }}
                                                className='product-depatment'
                                            />

                                        </div>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>

            </div>


        </div>

    </>
    );
};

export default Hardware;