/* eslint-disable no-prototype-builtins */
/* eslint-disable no-debugger */
/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import React, { useState, useEffect } from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import swal from 'sweetalert2';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import { AiOutlineClose } from 'react-icons/ai';
import Select from 'react-select';
import { InvoiceSettingsInit, ReasonCodeInit,CustomerOrderInit } from 'Model/SetupScreen.model';
import { ReasonCodeTypes } from 'Utility/ListCollection';
import * as StringHelpers from 'Helpers/StringHelpers';
import * as SetupScreenAPI from 'Services/SetupScreen';
import { NodataIndicator } from 'Components/Utility';
import { InputSelect, InputCheckBox, InputCurrency } from 'Components/Utility/CustomSelect';
import DefaultConfig from 'Config/GlobalVariable';

const Invoicesettings = (props) => {

  const [value, setValue] = useState(0);
  const [InvoiceSettings, setInvoiceSettings] = useState(InvoiceSettingsInit);

  const [ReasonCode, setReasonCode] = useState(ReasonCodeInit);
  const [CustomerOrder, setCustomerOrder] = useState(CustomerOrderInit);
  const [ReasonCodeList, setReasonCodeList] = useState([]);
  const [CustomerList, setCustomerList] = useState([]);

  const GetReasonCodelist = () => SetupScreenAPI.GetAllReasonCode().then(res => { setReasonCodeList(res.data.result); }).catch();
  const GetCustomerlist = () => SetupScreenAPI.GetAllCustomOrder().then(res => { setCustomerList(res.data.result); }).catch();

   useEffect(() => {
     GetCustomerlist();
  }, []);

  useEffect(() => {
    setInvoiceSettings({
      ...InvoiceSettings,
      store: StringHelpers.UpdateObject(InvoiceSettings.store, props.SetupScreenVal.store),
      terminal: StringHelpers.UpdateObject(InvoiceSettings.terminal, props.SetupScreenVal.terminal)
    });
  }, [props.SetupScreenVal]);

  const ChangeStoreHandler = (evt) => {
    const value = evt.target.value;
    setInvoiceSettings({
      ...InvoiceSettings,
      store: { ...InvoiceSettings.store, [evt.target.name]: value }
    });
  };

  const ChangeTerminalHandler = (evt) => {
    const value = evt.target.value;
    setInvoiceSettings({
      ...InvoiceSettings,
      terminal: { ...InvoiceSettings.terminal, [evt.target.name]: value }
    });
  };

  const handletabChange = (event, newValue) => setValue(newValue);
  const [showReasonCodeModel, SetshowReasonCodeModel] = useState(false);

  const AddReasonCode = () => {
    const obj = Object.assign({}, ReasonCode);
    SetupScreenAPI.PostReasonCode(obj).then(res => {
      setReasonCode(ReasonCodeInit);
      GetReasonCodelist();
    }).catch(err=>{
      if (err.response.data.errors.hasOwnProperty('Description')) {
        swal.fire({ title: err.response.data.errors.Description[0], ...DefaultConfig.SwalGlobal });
    }
    });
  };
  const DeleteReasonCode = (id) => {

    swal.fire({
      title: 'Are you sure you want to delete?',
      showCancelButton: true,
      confirmButtonText: 'Yes',
      cancelButtonText: 'No',
      preConfirm: () => {
        swal.showLoading();
        swal.getTitle().hidden = true;
        swal.getCancelButton().hidden = true;
       return SetupScreenAPI.DeleteReasonCode(id).then(res => {
          GetReasonCodelist();
          return true;
        }).catch(err=>{
          return true;
        });
      },
      ...DefaultConfig.SwalGlobalwithConfirm
    });
  };



  const AddCustomerOrder = () => {
    const obj = Object.assign({}, CustomerOrder);
    SetupScreenAPI.PostCustomOrder(obj).then(res => {
      setCustomerOrder(CustomerOrderInit);
      GetCustomerlist();
    }).catch(err=>{
      if (err.response.data.errors.hasOwnProperty('Description')) {
        swal.fire({ title: err.response.data.errors.Description[0], ...DefaultConfig.SwalGlobal });
    }
    });
  };
  const DeleteCustomerOrder = (id) => {

    swal.fire({
      title: 'Are you sure you want to delete?',
      showCancelButton: true,
      confirmButtonText: 'Yes',
      cancelButtonText: 'No',
      preConfirm: () => {
        swal.showLoading();
        swal.getTitle().hidden = true;
        swal.getCancelButton().hidden = true;
        return SetupScreenAPI.DeleteCustomOrder(id).then(res => {
          GetCustomerlist();
          return true;
        }).catch(err=>{
          return true;
        });
      },
      ...DefaultConfig.SwalGlobalwithConfirm
    });
  };

  function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }

  return (
    <div id='pos_view' className='recepit-page'>
      <div className='top-filter-row filter-tw'>
        <div className='lt-title-bx'>
          <h2>Invoice Settings</h2>
        </div>
        <div className='rt-filter-bx filter-hd'>
          <div className='row searchbar-mainview'>
            <div className='icons-list'>
              <button className='icons-hd-cm checks-icons' onClick={() => {
                props.UpdateSetting(InvoiceSettings);
              }}></button>
            </div>
          </div>
        </div>
      </div>
      <hr className='full-width' />
      <Box className='tabs-cm tab-tw' sx={{ width: '100%' }}>
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <Tabs value={value} onChange={handletabChange} aria-label="basic tabs example">
            <Tab label="page 1" {...a11yProps(0)} />
            <Tab label="page 2" {...a11yProps(1)} />
            {/* <Tab label="page 3" {...a11yProps(2)} /> */}
          </Tabs>
        </Box>
        <Box className='tabs-panel'>

          {value === 0 && (
            <div id='pos_view' className='invoice-setting-1'>
              <div className='row table-cm mt-0'>
                <div className="col-md-4">
                  <div className="info-bx info-bx-width">
                    <div className="form-check">
                      <label className="form-check-label">Use Mix N Match</label>
                      <InputCheckBox className="form-check-input"
                        name='useMixNMatch'
                        checked={InvoiceSettings.store.useMixNMatch}
                        onChange={(e) => {
                          ChangeStoreHandler(e);
                        }} />
                    </div>
                  </div>
                </div>
                <div className="col-md-4">
                  <div className="info-bx info-bx-width">
                    <div className="form-check">
                      <label className="form-check-label">Combine Lines</label>
                      <InputCheckBox className="form-check-input"
                        name='combineLines'
                        checked={InvoiceSettings.store.combineLines}
                        onChange={(e) => {
                          ChangeStoreHandler(e);
                        }} />
                    </div>
                  </div>
                </div>
                <div className="col-md-4">
                  <div className="info-bx info-bx-width">
                    <div className="form-check">
                      <label className="form-check-label">Prompt Amount Tendered</label>
                      <InputCheckBox className="form-check-input"
                        name='amountPrompt'
                        checked={InvoiceSettings.terminal.amountPrompt}
                        onChange={(e) => {
                          ChangeTerminalHandler(e);
                        }} />
                    </div>
                  </div>
                </div>

                <div className="col-md-4">
                  <div className="info-bx info-bx-width">
                    <div className="form-check">
                      <label className="form-check-label">Stock Prompt</label>
                      <InputCheckBox className="form-check-input"
                        name='stockPrompt'
                        checked={InvoiceSettings.terminal.stockPrompt}
                        onChange={(e) => {
                          ChangeTerminalHandler(e);
                        }} />
                    </div>
                  </div>
                </div>

                <div className="col-md-4 d-none">
                  <div className="info-bx info-bx-width">
                    <div className="form-check">
                      <label className="form-check-label">Prompt Customer Information on Pinpad</label>
                      <InputCheckBox className="form-check-input"
                        name='customerPrompt'
                        checked={InvoiceSettings.terminal.customerPrompt}
                        onChange={(e) => {
                          ChangeTerminalHandler(e);
                        }} />
                    </div>
                  </div>
                </div>

                <div className="col-md-4">
                  <div className="info-bx info-bx-width">
                    <div className="form-check">
                      <label className="form-check-label">Exempt Tax form Foodstamp Purchases</label>
                      <InputCheckBox className="form-check-input"
                        name='exemptTax'
                        checked={InvoiceSettings.store.exemptTax}
                        onChange={(e) => {
                          ChangeStoreHandler(e);
                        }} />
                    </div>
                  </div>
                </div>

                <div className="col-md-4">
                  <div className="info-bx info-bx-width">
                    <div className="form-check">
                      <label className="form-check-label">Show Scanned Alt SKU on Invoice</label>
                      <InputCheckBox className="form-check-input"
                        name='skuOnInvoice'
                        checked={InvoiceSettings.store.skuOnInvoice}
                        onChange={(e) => {
                          ChangeStoreHandler(e);
                        }} />
                    </div>
                  </div>
                </div>
                <div className="col-md-4 d-none">
                  <div className="info-bx info-bx-width">
                    <div className="form-check">
                      <label className="form-check-label">Prompt Reason</label>
                      <InputCheckBox className="form-check-input"
                        name='promptReason'
                        checked={InvoiceSettings.store.promptReason}
                        onChange={(e) => {
                          ChangeStoreHandler(e);
                        }} />
                      <button className="save-btn save-btn2 btn btn-save d-none" onClick={() => {
                        GetReasonCodelist();
                        SetshowReasonCodeModel(true);
                      }}>Reason Codes</button>
                    </div>
                  </div>
                </div>
                <div className="col-md-12 mb-3">
                  <hr className='full-width mt-3' />
                </div>
                <div className="col-md-4">
                  <div className='row'>
                    <div className="col-md-12 mb-2">
                      <span className="title-tab">Log Exceptions for:</span>
                      <hr className='full-width mt-3' />
                    </div>
                    <div className="col-md-12">
                      <div className="info-bx info-bx-width">
                        <div className="form-check mb-3">
                          <label className="form-check-label">Line Item Deletion</label>
                          <InputCheckBox className="form-check-input"
                            name='lineItemDeletion'
                            checked={InvoiceSettings.store.lineItemDeletion}
                            onChange={(e) => {
                              ChangeStoreHandler(e);
                            }} />
                        </div>
                      </div>
                    </div>
                    <div className="col-md-12 d-none">
                      <div className="info-bx info-bx-width">
                        <div className="form-check mb-3">
                          <label className="form-check-label">Line Item Discounts</label>
                          <InputCheckBox className="form-check-input"
                            name='lineItemDisc'
                            checked={InvoiceSettings.store.lineItemDisc}
                            onChange={(e) => {
                              ChangeStoreHandler(e);
                            }} />
                        </div>
                      </div>
                    </div>
                    <div className="col-md-12">
                      <div className="info-bx info-bx-width">
                        <div className="form-check mb-3">
                          <label className="form-check-label">No Sale</label>
                          <InputCheckBox className="form-check-input"
                            name='noSale'
                            checked={InvoiceSettings.store.noSale}
                            onChange={(e) => {
                              ChangeStoreHandler(e);
                            }} />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-md-4 d-none">
                  <div className='row'>
                    <div className="col-md-12 mb-2">
                      <span className="title-tab">Line Discount Prompt</span>
                      <hr className='full-width mt-3' />
                    </div>
                    <div className="col-md-6">
                      <div className="info-bx info-bx-width">
                        <div className="form-check mb-0">
                          <label className="form-check-label">No reason code</label>
                          <input
                            className="form-check-input"
                            checked={InvoiceSettings.store.lineDiscPrompt === 0}
                            value={0}
                            name="lineDiscPrompt"
                            type="radio"
                            onChange={(e) => {
                              let obj = { target: { name: e.target.name, value: parseInt(e.target.value) } };
                              ChangeStoreHandler(obj);
                              // props.handleChange(e.target.name, 2);
                            }}
                          ></input>
                        </div>
                      </div>
                    </div>
                    <div className='clearfix'></div>
                    <div className="col-md-6">
                      <div className="info-bx info-bx-width">
                        <div className="form-check mb-0 mt-3">
                          <label className="form-check-label">Ask for reason code</label>
                          <input
                            className="form-check-input"
                            checked={InvoiceSettings.terminal.lineDiscPrompt === 1}
                            value={1}
                            name="lineDiscPrompt"
                            type="radio"
                            onChange={(e) => {
                              let obj = { target: { name: e.target.name, value: parseInt(e.target.value) } };
                              ChangeTerminalHandler(obj);
                              // props.handleChange(e.target.name, 2);
                            }}
                          ></input>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-md-4 d-none">
                  <div className='row'>
                    <div className="col-md-12 mb-2">
                      <span className="title-tab">Idle Log Out</span>
                      <hr className='full-width mt-3' />
                    </div>
                    <div className="col-md-4">
                      <div className="info-bx">
                        <div className="form-check row mb-0 mt-2">
                          <div className='col-md-8'>
                            <input type='text'
                              name='idleLogOutMin'
                              pattern="[0-9]*"
                              value={InvoiceSettings.terminal.idleLogOutMin}
                              onChange={(e) => {
                                if (e.target.validity.valid) {
                                  ChangeTerminalHandler(e);
                                }
                              }}
                              placeholder='0'
                              className='form-control' />
                          </div>
                          <label className="form-check-label col-md-4">minutes</label>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* {value === 1 && (
            <div id='pos_view' className='invoice-setting-2'>
              <div className='row table-cm mt-0'>
                <div className='form-group col-md-4'>
                  <div className='row'>
                    <label className='col-sm-12 col-form-label'>Amount That Flags Item as a Large Purchase</label>
                    <div className='col-md-12'>
                      <InputCurrency
                        className='form-control'
                        prefix='$'
                        data-number-to-fixed="5"
                        data-number-stepfactor="100"
                        name="largePurchaseAmount"
                        value={InvoiceSettings.store.largePurchaseAmount}
                        placeholder=""
                        onValueChange={(e) => {
                          ChangeStoreHandler(e);
                        }}
                        allowDecimals
                        decimalsLimit="5"
                        disableAbbreviations
                      />

                    </div>
                  </div>
                </div>
              </div>
            </div>
          )} */}

          {value === 1 && (
            <div id='pos_view' className='invoice-setting-3'>
              <div className='row table-cm mt-0'>
                <div className="col-md-6">
                  <div className="info-bx info-bx-width-small">
                    <div className="form-check mb-0 mt-3">
                      <label className="form-check-label">Accept Custom Orders</label>
                      <InputCheckBox className="form-check-input"
                        name='acceptCustOrders'
                        checked={InvoiceSettings.terminal.acceptCustOrders}
                        onChange={(e) => {
                          ChangeTerminalHandler(e);
                        }} />
                    </div>
                  </div>
                </div>
                <div className='col-md-6 d-flex align-items-center justify-content-end mt-0'>
                  <div className='form-group col-md-10'>
                    <div className='row justify-content-end'>
                      <label className='col-sm-4 col-form-label'>Enter Type Code</label>
                      <div className='col-md-8 add-code-bx p-0'>
                        <input type='description'
                        disabled={!InvoiceSettings.terminal.acceptCustOrders}
                        value={CustomerOrder.description}
                          name='Enter Type Code'
                          onChange={(e)=>{
                            setCustomerOrder({...CustomerOrder,description:e.target.value});
                          }}
                          placeholder=''
                          className='form-control' />
                        <button className="save-btn save-btn2 btn btn-save"
                        disabled={!InvoiceSettings.terminal.acceptCustOrders}
                        onClick={()=>{
                          AddCustomerOrder();
                        }}>Add</button>
                      </div>
                    </div>
                  </div>

                </div>
                <div className="col-md-12 mt-1">
                  <div className="margin-depart table-sm table-cm">
                    <div className='react-bootstrap-table'>
                      <table className="table table-bordered">
                        <thead>
                          <tr>
                            <th width="80%">Custom Order Platforms</th>
                            <th width="20%">Action</th>
                          </tr>
                        </thead>
                        <tbody>
                        {CustomerList.length ?
                          (CustomerList.map((item, index) => {
                            return (
                              <tr key={index}>
                                <td width="80%">{item.description}</td>
                                <td width="20%"><div className="action-btn">
                                  <button className="delete-icons" onClick={(e) => {
                                    DeleteCustomerOrder(item.customOrderId);
                                  }}><span></span></button>
                                </div>
                                </td>
                              </tr>
                            );
                          })) : (
                            <tr>
                              <td colSpan="4" style={{ textAlign: 'center' }}><NodataIndicator /></td>
                            </tr>
                          )
                        }
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}
        </Box>
      </Box>
      <Modal show={showReasonCodeModel} onShow={() => {
        setReasonCode(ReasonCodeInit);
      }} className="table-cm cm-popup modal-lg category-popup reason-popup">
        <Modal.Header>
          <Modal.Title><span>Setup Reason Codes</span><Button variant="closepopup" onClick={() => {
            SetshowReasonCodeModel(false);
          }}><AiOutlineClose style={{ fontSize: '1.3020833333333333vw' }} /></Button></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="container">
            <div className="margin-popup" />
            <div className='row'>

              <div className='form-group col-md-10'>
                <div className='row'>
                  <label className='col-sm-3 col-form-label'>Enter Type Code</label>
                  <div className='col-md-8'>
                    <input type='text'
                      value={ReasonCode.description}
                      name='description'
                      onChange={(e) => {
                        setReasonCode({ ...ReasonCode, description: e.target.value });
                      }}
                      placeholder='000'
                      className='form-control' />
                  </div>
                </div>
              </div>
              <div className='col-md-2 right-add-btn'>
                <Button className="save-btn" variant="save" onClick={() => {
                  AddReasonCode();
                }}>ADD</Button>
              </div>
            </div>
            <div className='row'>
              <div className="col-md-10 mt-2">
                <div className='form-group department-row'>
                  <div className='row'>
                    <label htmlFor='departmentId' className='col-sm-3 col-form-label'>Reason Code Type</label>
                    <div className='col-md-8' style={{ display: 'flex' }}>
                      <InputSelect options={ReasonCodeTypes}
                        name="addCustomers"
                        value={ReasonCodeTypes.filter(({ value }) => value === ReasonCode.reasonCodeType)}
                        onChange={(e) => {
                          setReasonCode({ ...ReasonCode, reasonCodeType: e.target.value });
                        }} className='product-depatment' />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className='row'>
              <div className='col-md-12'>
                <div className="margin-depart table-sm table-cm">
                  <div className='react-bootstrap-table'>
                    <table className="table table-bordered">
                      <thead>
                        <tr>
                          <th width="80%">Reason Codes</th>
                          <th width="20%">Action</th>
                        </tr>
                      </thead>
                      <tbody>
                        {ReasonCodeList.length ?
                          (ReasonCodeList.map((item, index) => {
                            return (
                              <tr key={index}>
                                <td width="80%">{item.description}</td>
                                <td width="20%"><div className="action-btn">
                                  <button className="delete-icons" onClick={(e) => {
                                    DeleteReasonCode(item.reasonCodeId);
                                  }}><span></span></button>
                                </div>
                                </td>
                              </tr>
                            );
                          })) : (
                            <tr>
                              <td colSpan="4" style={{ textAlign: 'center' }}><NodataIndicator /></td>
                            </tr>
                          )
                        }
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>

        </Modal.Body>
      </Modal>

    </div>

  );
};

export default Invoicesettings;