/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import { useState } from 'react';
import AccountControl from './AccountControl';
import Hardware from './Hardware';
import Inventory from './Inventory';
import InvoiceSettings from './InvoiceSettings';
import PaymentProcessing from './PaymentProcessing';
import QuickInvoiceingAlert from './QuickInvoiceingAlert';
import QuickAdd from './QuickAdd';
import Receipt from './Receipt';
import Reports from './Reports';
import SystemAccess from './SystemAccess';
import TouchScreen from './TouchScreen';
import ScreenDispalySetup from './ScreenDispalySetup';
import * as SetupScreenAPI from 'Services/SetupScreen';
import { SetupScreenInit } from 'Model/SetupScreen.model';
import { useEffect } from 'react';
import swal from 'sweetalert2';
import DefaultConfig from 'Config/GlobalVariable';
import { LoadSetting, setLoading } from 'Context/Context.action';
import { useUser } from 'Context/UserContext';

let TabValue = 0;

const SetupScreen = () => {

    const [value, setValue] = useState(TabValue);
    const [userState, userDispatch] = useUser();
    const [SetupScreen, setSetupScreen] = useState(SetupScreenInit);

    const GetAllSetting = () => SetupScreenAPI.GetAllSetting().then(res => setSetupScreen(res.data.result)).catch(err => { console.log(err); });
    const UpdateSetting = (data) => SetupScreenAPI.PatchAllSetting(data).then( async(res) => {
        swal.fire({
            title: 'Setting update successfully.',
            ...DefaultConfig.SwalGlobal
        });
        await LoadSetting(userDispatch);
        setSetupScreen(res.data.result);
    }).catch(err => { console.log(err); });

    const UpdateAllSetting = (data) =>{
        SetupScreenAPI.PatchAllSetting().then().catch(); 
    };

    useEffect(() => {
        GetAllSetting();
    }, []);

    const handleChange = (evt) => {
        let value = evt.target.value;
        setSetupScreen({
            ...SetupScreen,
            [evt.target.name]: value
        });
    };


    const handleTabChange = (event, newValue) => {
        setValue(newValue);
        TabValue = newValue;
    };

    function TabPanel(props) {
        const { children, value, index, ...other } = props;

        return (
            <div
                role="tabpanel"
                hidden={value !== index}
                id={`vertical-tabpanel-${index}`}
                aria-labelledby={`vertical-tab-${index}`}
                {...other}
            >
                {value === index && (
                    <Box sx={{ p: 3 }}>
                        <Typography>{children}</Typography>
                    </Box>
                )}
            </div>
        );
    }

    function a11yProps(index) {
        return {
            id: `vertical-tab-${index}`,
            'aria-controls': `vertical-tabpanel-${index}`,
        };
    }
    return (
        <div id="pos_view" className='setting-page setting-page-inner'>
            <div className="card-box-listing">
                <Box className='tabs-cm'
                    sx={{ flexGrow: 1, bgcolor: 'background.paper', display: 'flex', height: 'auto' }}
                >
                    <Tabs
                        orientation="vertical"
                        variant="scrollable"
                        value={value}
                        onChange={handleTabChange}
                        aria-label="Vertical tabs example"
                        sx={{ borderRight: 1, borderColor: 'divider' }}
                    >
                        {/* <Tab label="Account Control" {...a11yProps(0)} /> */}
                        <Tab label="Hardware" {...a11yProps(0)} />
                        <Tab label="Inventory" {...a11yProps(1)} />
                        <Tab label="Invoice Settings" {...a11yProps(2)} />
                        <Tab label="Payment Processing" {...a11yProps(3)} />
                        <Tab label="Quick Invoicing & Alert" {...a11yProps(4)} />
                        <Tab label="Quick-Add" {...a11yProps(5)} />
                        <Tab label="Receipt" {...a11yProps(6)} />
                        <Tab label="Reports" {...a11yProps(7)} />
                        <Tab label="System Access" {...a11yProps(8)} />
                        {/* <Tab label="Touch Screen" {...a11yProps(9)} /> */}
                        {/* <Tab label="Screen Dispaly Setup" {...a11yProps(11)} /> */}
                        
                    </Tabs>
                    {/* <TabPanel value={value} index={0} className="home-tab">
                        <div className="parent-for-list row product-price-bx">
                            <AccountControl
                                handleChange={handleChange}
                                SetupScreenVal={SetupScreen}
                                UpdateSetting={UpdateSetting}
                            />
                        </div>
                    </TabPanel> */}
                    <TabPanel value={value} index={0} className="home-tab">
                        <div className="parent-for-list row product-price-bx">
                            <Hardware
                                handleChange={handleChange}
                                SetupScreenVal={SetupScreen}
                                UpdateSetting={UpdateSetting}
                            />
                        </div>
                    </TabPanel>
                    <TabPanel value={value} index={1} className="home-tab">
                        <div className="parent-for-list row product-price-bx">
                            <Inventory
                                handleChange={handleChange}
                                SetupScreenVal={SetupScreen}
                                UpdateSetting={UpdateSetting}
                            />
                        </div>
                    </TabPanel>
                    <TabPanel value={value} index={2} className="home-tab">
                        <div className="parent-for-list row product-price-bx">
                            <InvoiceSettings
                                handleChange={handleChange}
                                SetupScreenVal={SetupScreen}
                                UpdateSetting={UpdateSetting}
                            />
                        </div>
                    </TabPanel>
                    <TabPanel value={value} index={3} className="home-tab">
                        <div className="parent-for-list row product-price-bx">
                            <PaymentProcessing
                                handleChange={handleChange}
                                SetupScreenVal={SetupScreen}
                                UpdateSetting={UpdateSetting}
                            />
                        </div>
                    </TabPanel>
                    <TabPanel value={value} index={4} className="home-tab">
                        <div className="parent-for-list row product-price-bx">
                            <QuickInvoiceingAlert
                                handleChange={handleChange}
                                SetupScreenVal={SetupScreen}
                                UpdateSetting={UpdateSetting}
                            />
                        </div>
                    </TabPanel>
                    <TabPanel value={value} index={5} className="home-tab">
                        <div className="parent-for-list row product-price-bx">
                            <QuickAdd
                                handleChange={handleChange}
                                SetupScreenVal={SetupScreen}
                                UpdateSetting={UpdateSetting}
                            />
                        </div>
                    </TabPanel>

                    <TabPanel value={value} index={6} className="home-tab">
                        <div className="parent-for-list row product-price-bx">
                            <Receipt
                                handleChange={handleChange}
                                SetupScreenVal={SetupScreen}
                                UpdateSetting={UpdateSetting}
                            />
                        </div>
                    </TabPanel>
                    <TabPanel value={value} index={7} className="home-tab">
                        <div className="parent-for-list row product-price-bx">
                            <Reports
                                handleChange={handleChange}
                                SetupScreenVal={SetupScreen}
                                UpdateSetting={UpdateSetting}
                            />
                        </div>
                    </TabPanel>
                    <TabPanel value={value} index={8} className="home-tab">
                        <div className="parent-for-list row product-price-bx">
                            <SystemAccess
                                handleChange={handleChange}
                                SetupScreenVal={SetupScreen}
                                UpdateSetting={UpdateSetting}
                            />
                        </div>
                    </TabPanel>
                    {/* <TabPanel value={value} index={9} className="home-tab">
                        <div className="parent-for-list row product-price-bx">
                            <TouchScreen
                                handleChange={handleChange}
                                SetupScreenVal={SetupScreen}
                                UpdateSetting={UpdateSetting}
                            />
                        </div>
                    </TabPanel> */}
                    <TabPanel value={value} index={10} className="home-tab">
                        <div className="parent-for-list row product-price-bx">
                            <ScreenDispalySetup
                                handleChange={handleChange}
                                SetupScreenVal={SetupScreen}
                                UpdateSetting={UpdateSetting}
                            />
                        </div>
                    </TabPanel>
                </Box>
            </div>
        </div>
    );
};

export default SetupScreen;