/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import React, { useState,useEffect } from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import { InputCheckBox,InputCurrency } from 'Components/Utility/CustomSelect';
import * as StringHelpers from 'Helpers/StringHelpers';
import { SystemAccessInit } from 'Model/SetupScreen.model';

const SystemAccess = (props) => {
  const [value, setValue] = useState(0);
  const [SystemAccess, setSystemAccess] = useState(SystemAccessInit);

  useEffect(() => {
    setSystemAccess({
      ...SystemAccess,
      store: StringHelpers.UpdateObject(SystemAccess.store, props.SetupScreenVal.store),
    });
  }, [props.SetupScreenVal]);

  const ChangeStoreHandler = (evt) => {
    const value = evt.target.value;
    setSystemAccess({
      ...SystemAccess,
      store: { ...SystemAccess.store, [evt.target.name]: value }
    });
  };

  const handletabChange = (event, newValue) => setValue(newValue);

  function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }
  return (
    <div id='pos_view' className='system-page'>
      <div className='top-filter-row filter-tw'>
        <div className='lt-title-bx'>
          <h2>System Access</h2>
        </div>
        <div className='rt-filter-bx filter-hd'>
          <div className='row searchbar-mainview'>
            <div className='icons-list'>
              <button className='icons-hd-cm checks-icons' onClick={() => {
                props.UpdateSetting(SystemAccess);
              }}></button>
            </div>
          </div>
        </div>
      </div>
      <hr className='full-width' />
      <Box className='tabs-cm tab-tw' sx={{ width: '100%' }}>
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <Tabs value={value} onChange={handletabChange} aria-label="basic tabs example">
            <Tab label="Access" {...a11yProps(0)} />
            <Tab label="Time Clock" {...a11yProps(1)} />
          </Tabs>
        </Box>
        <Box className='tabs-panel'>
          {value === 0 && (
            <div id='pos_view' className='Access-tab1'>
              <div className='row table-cm mt-0'>
                <div className='form-group col-md-6'>
                  <div className='row'>
                    <label className='col-sm-4 col-form-label'>Default Cash Drawer Start</label>
                    <div className='col-md-7'>
                    <InputCurrency
                      className='form-control'
                      prefix='$'
                      data-number-to-fixed="5"
                      data-number-stepfactor="100"
                      name="defaultCashDrawerStart"
                      value={SystemAccess.store.defaultCashDrawerStart}
                      placeholder=""
                      onValueChange={(e) => {
                        ChangeStoreHandler(e);
                      }}
                      allowDecimals
                      decimalsLimit="5"
                      disableAbbreviations
                    />

                    </div>
                  </div>
                </div>

                <div className="col-md-4">
                  <div className="info-bx info-bx-width">
                    <div className="form-check mt-2">
                      <label className="form-check-label">Clockout Receipt</label>
                      <InputCheckBox className="form-check-input"
                        name='clockoutReceipt'
                        checked={SystemAccess.store.clockoutReceipt}
                        onChange={(e) => {
                          ChangeStoreHandler(e);
                        }} />
                    </div>
                  </div>
                </div>

                <div className='clearfix'></div>

                <div className='form-group col-md-6'>
                  <div className='row'>
                    <label className='col-sm-4 col-form-label'>Recount threshold</label>
                    <div className='col-md-7'>
                    <InputCurrency
                      className='form-control'
                      prefix='$'
                      data-number-to-fixed="5"
                      data-number-stepfactor="100"
                      name="blindRecountValue"
                      value={SystemAccess.store.blindRecountValue}
                      placeholder=""
                      onValueChange={(e) => {
                        ChangeStoreHandler(e);
                      }}
                      allowDecimals
                      decimalsLimit="5"
                      disableAbbreviations
                    />

                    </div>
                  </div>
                </div>

                <div className="col-md-4">
                  <div className="info-bx info-bx-width">
                    <div className="form-check mt-2">
                      <label className="form-check-label">Blind recount if drawer over/short</label>
                      <InputCheckBox className="form-check-input"
                        name='blindRecount'
                        checked={SystemAccess.store.blindRecount}
                        onChange={(e) => {
                          ChangeStoreHandler(e);
                        }} />

                    </div>
                  </div>
                </div>

                <div className="col-md-12 mb-2">
                  <hr className='full-width mt-3' />
                </div>

                <div className="col-md-4">
                  <div className='row'>
                    <div className="col-md-12 mb-2">
                      <span className="title-tab">When using Employee Cash Drawers...</span>
                      <hr className='full-width mt-3' />
                    </div>
                    <div className="col-md-12">
                      <div className="info-bx info-bx-width">
                        <div className="form-check mb-3">
                          <label className="form-check-label">Assign Drawer During Clock-In</label>
                          <input
                            className="form-check-input"
                            value="0"
                            checked={SystemAccess.store.employeeCashDrawers === 0}
                            name="employeeCashDrawers"
                            type="radio"
                            onChange={() => {
                              setSystemAccess({
                                ...SystemAccess,
                                store: { ...SystemAccess.store, 'employeeCashDrawers': 0 }
                              });
                            }}
                          ></input>

                        </div>
                      </div>
                    </div>

                    <div className="col-md-12">
                      <div className="info-bx info-bx-width">
                        <div className="form-check mb-3">
                          <label className="form-check-label">Assign Drawer During First Login</label>
                          <input
                            className="form-check-input"
                            value="1"
                            checked={SystemAccess.store.employeeCashDrawers === 1}
                            name="employeeCashDrawers"
                            type="radio"
                            onChange={() => {
                              setSystemAccess({
                                ...SystemAccess,
                                store: { ...SystemAccess.store, 'employeeCashDrawers': 1 }
                              });
                            }}
                          ></input>

                        </div>
                      </div>
                    </div>

                  </div>
                </div>
                <div className="col-md-1">
                </div>
                <div className="col-md-4">
                  <div className='row'>
                    <div className="col-md-12 mb-2">
                      <span className="title-tab">Security Method</span>
                      <hr className='full-width mt-3' />
                    </div>
                    <div className="col-md-12">
                      <div className="info-bx info-bx-width">
                        <div className="form-check mb-3">
                          <label className="form-check-label">Use Permissions of current job code</label>
                          <input className="form-check-input" 
                          checked={true}
                          disabled={true}
                          type="checkbox"></input>
                        </div>
                      </div>
                    </div>


                  </div>
                </div>

              </div>
            </div>
          )}
          {value === 1 && (
            <div id='pos_view' className='Timeclock-page'>
              <div className='row table-cm mt-0'>

                <div className="col-md-4">
                  <div className='row'>
                    <div className="col-md-12 mb-2">
                      <span className="title-tab">EOD -- Require Money Count</span>
                      <hr className='full-width mt-3' />
                    </div>
                    <div className="col-md-12">
                      <div className="info-bx info-bx-width">
                        <div className="form-check mb-3">
                          <label className="form-check-label">Assume correct value</label>
                          <input className="form-check-input" checked={true} disabled={true} type="checkbox"></input>
                        </div>
                      </div>
                    </div>

                  </div>
                </div>
                <div className="col-md-1"></div>
                <div className="col-md-4">
                  <div className='row'>
                    <div className="col-md-12 mb-2">
                      <span className="title-tab">Shift Assignment</span>
                      <hr className='full-width mt-3' />
                    </div>
                    <div className="col-md-12">
                      <div className="info-bx info-bx-width">
                        <div className="form-check mb-3">
                          <label className="form-check-label">Don&apos;t track shifts</label>
                          <input
                            className="form-check-input"
                            value="0"
                            checked={SystemAccess.store.shiftAssignment === 0}
                            name="shiftAssignment"
                            type="radio"
                            onChange={() => {
                              setSystemAccess({
                                ...SystemAccess,
                                store: { ...SystemAccess.store, 'shiftAssignment': 0 }
                              });
                            }}
                          ></input>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-12">
                      <div className="info-bx info-bx-width">
                        <div className="form-check mb-3">
                          <label className="form-check-label">Track shifts by Cashier</label>
                          <input
                            className="form-check-input"
                            value="1"
                            checked={SystemAccess.store.shiftAssignment === 1}
                            name="shiftAssignment"
                            type="radio"
                            onChange={() => {
                              setSystemAccess({
                                ...SystemAccess,
                                store: { ...SystemAccess.store, 'shiftAssignment': 1 }
                              });
                            }}
                          ></input>
                        </div>
                      </div>
                    </div>

                    <div className="col-md-12">
                      <div className="info-bx info-bx-width">
                        <div className="form-check mb-3">
                          <label className="form-check-label">Track shifts by Station</label>
                          <input
                            className="form-check-input"
                            value="2"
                            checked={SystemAccess.store.shiftAssignment === 2}
                            name="shiftAssignment"
                            type="radio"
                            onChange={() => {
                              setSystemAccess({
                                ...SystemAccess,
                                store: { ...SystemAccess.store, 'shiftAssignment': 2 }
                              });
                            }}
                          ></input>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}
        </Box>
      </Box>
    </div>
  );
};

export default SystemAccess;