/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import React from 'react';
import TextField from '@mui/material/TextField';
import { ReportsInit } from 'Model/SetupScreen.model';
import * as StringHelpers from 'Helpers/StringHelpers';
import { useState, useEffect } from 'react';
import { InputCheckBox } from 'Components/Utility/CustomSelect';


const Reports = (props) => {

  const [Report, setReport] = useState(ReportsInit);

  useEffect(() => {
    setReport({
      ...Report,
      store: StringHelpers.UpdateObject(Report.store, props.SetupScreenVal.store),
    });
  }, [props.SetupScreenVal]);

  const ChangeStoreHandler = (evt) => {
    const value = evt.target.value;
    setReport({
      ...Report,
      store: { ...Report.store, [evt.target.name]: value }
    });
  };

  return (
    <div id='pos_view' className='reports-page'>
      <div className='top-filter-row filter-tw'>
        <div className='lt-title-bx'>
          <h2>Reports</h2>
        </div>
        <div className='rt-filter-bx filter-hd'>
          <div className='row searchbar-mainview'>
            <div className='icons-list'>
              <button className='icons-hd-cm checks-icons' onClick={() => {
                props.UpdateSetting(Report);
              }}></button>
            </div>
          </div>
        </div>
      </div>
      <hr className='full-width' />
      <div className='row table-cm mt-0'>
        <div className="col-md-12">
          <div className='row'>
            <div className="col-md-12 mb-4">
              <span className="title-tab">Default Start and End Date/Time</span>
            </div>
            <div className='col-sm-2'>
              <div className='date-row'>
                <div className="col-sm-11 time-picker p-0">
                  <div className='row mb-2'>
                    <div className="col-md-12 mb-2">
                      <span className="title-tab small-size">Start Date/Time</span>
                      <hr className='full-width full-width2' />
                    </div>
                    <div className="col-md-4">
                      <div className="info-bx info-bx-width-small">
                        <div className="form-check mb-0">
                          <label className="form-check-label">Yesterday</label>
                          <input
                            className="form-check-input"
                            value="0"
                            checked={Report.store.startDate === 0}
                            name="startDate"
                            type="radio"
                            onChange={() => {
                              setReport({
                                ...Report,
                                store: { ...Report.store, 'startDate': 0 }
                              });
                            }}
                          ></input>
                        </div>
                      </div>
                    </div>
                    <div className='clearfix'></div>
                    <div className="col-md-4 mt-3 mb-4">
                      <div className="info-bx info-bx-width-small">
                        <div className="form-check mb-0">
                          <label className="form-check-label">Today</label>
                          <input
                            className="form-check-input"
                            value="1"
                            checked={Report.store.startDate === 1}
                            name="startDate"
                            type="radio"
                            onChange={() => {
                              setReport({
                                ...Report,
                                store: { ...Report.store, 'startDate': 1 }
                              });
                            }}
                          ></input>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className='row'>
                  <div className='col-md-12'>
                  <TextField
                    id="time"
                    label="Start Time"
                    type="time"
                    name='startTime'
                    value={Report.store.startTime}
                    onChange={(e) => {
                      ChangeStoreHandler(e);
                    }}
                    defaultValue="11:00"
                    InputLabelProps={{
                      shrink: true,
                    }}
                    inputProps={{
                      step: 300, // 5 min
                    }}
                    sx={{ width: 150 }}
                  />
                  </div>
                  </div>
                </div>
                <div className="col-sm-6 text-center">
                </div>
                <div className="col-sm-11 time-picker  p-0">
                  <div className='row mb-2'>
                    <div className="col-md-12 mb-2">
                      <span className="title-tab small-size">End Date/Time</span>
                      <hr className='full-width full-width2' />
                    </div>
                    <div className="col-md-4">
                      <div className="info-bx info-bx-width-small">
                        <div className="form-check mb-0">
                          <label className="form-check-label">Today</label>
                          <input
                            className="form-check-input"
                            value="1"
                            checked={Report.store.endDate === 1}
                            name="endDate"
                            type="radio"
                            onChange={() => {
                              setReport({
                                ...Report,
                                store: { ...Report.store, 'endDate': 1 }
                              });
                            }}
                          ></input>
                        </div>
                      </div>
                    </div>
                    <div className='clearfix'></div>
                    <div className="col-md-4 mt-3 mb-4">
                      <div className="info-bx info-bx-width-small">
                        <div className="form-check mb-0">
                          <label className="form-check-label">Tomorrow</label>
                          <input
                            className="form-check-input"
                            value="2"
                            checked={Report.store.endDate === 2}
                            name="endDate"
                            type="radio"
                            onChange={() => {
                              setReport({
                                ...Report,
                                store: { ...Report.store, 'endDate': 2 }
                              });
                            }}
                          ></input>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className='row'>
                  <div className='col-md-12'>
                  <TextField
                    id="time"
                    label="End Time"
                    type="time"
                    name='endTime'
                    value={Report.store.endTime}
                    onChange={(e) => {
                      ChangeStoreHandler(e);
                    }}
                    defaultValue="11:00"
                    InputLabelProps={{
                      shrink: true,
                    }}
                    inputProps={{
                      step: 300, // 5 min
                    }}
                    sx={{ width: 150 }}
                  />
                  </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-md-12 mb-0 mt-4">
          <hr className='full-width' />
        </div>
        <div className="col-md-4 mt-3">
          <div className='row'>
            <div className="col-md-12 mb-2">
              <span className="title-tab">End of day</span>
              <hr className='full-width mt-3' />
            </div>
            <div className="col-md-12">
              <div className="info-bx info-bx-width">
                <div className="form-check mb-3">
                  <label className="form-check-label">Don&apos;t print Sales totals</label>
                  <InputCheckBox className="form-check-input"
                    name='salesTotals'
                    checked={Report.store.salesTotals}
                    onChange={(e) => {
                      ChangeStoreHandler(e);
                    }} />
                </div>
              </div>
            </div>
            <div className="col-md-12">
              <div className="info-bx info-bx-width">
                <div className="form-check mb-3">
                  <label className="form-check-label">Don&apos;t print Deposit breakdown</label>
                  <InputCheckBox className="form-check-input"
                    name='depositBreakdown'
                    checked={Report.store.depositBreakdown}
                    onChange={(e) => {
                      ChangeStoreHandler(e);
                    }} />
                </div>
              </div>
            </div>
            <div className="col-md-12">
              <div className="info-bx info-bx-width">
                <div className="form-check mb-3">
                  <label className="form-check-label">Don&apos;t print Sales breakdown(itemized)</label>
                  <InputCheckBox className="form-check-input"
                    name='salesBreakdown'
                    checked={Report.store.salesBreakdown}
                    onChange={(e) => {
                      ChangeStoreHandler(e);
                    }} />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-md-1"></div>
        <div className="col-md-4 mt-3">
          <div className='row'>
            <div className="col-md-12 mb-2">
              <span className="title-tab">Print Report to Receipt Printer</span>
              <hr className='full-width mt-3' />
            </div>
            <div className="col-md-12">
              <div className="info-bx info-bx-width">
                <div className="form-check mb-3">
                  <label className="form-check-label">End of Day/Daily Close-Reprint Report</label>
                  <InputCheckBox className="form-check-input"
                    name='eodReport'
                    checked={Report.store.eodReport}
                    onChange={(e) => {
                      ChangeStoreHandler(e);
                    }} />
                </div>
              </div>
            </div>
            <div className="col-md-12">
              <div className="info-bx info-bx-width">
                <div className="form-check mb-3">
                  <label className="form-check-label">Financial Summary Report</label>
                  <InputCheckBox className="form-check-input"
                    name='finSummaryReport'
                    checked={Report.store.finSummaryReport}
                    onChange={(e) => {
                      ChangeStoreHandler(e);
                    }} />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Reports;